import React, { Component, useState } from "react";
import {
  Widget,
  renderCustomComponent,
  setQuickButtons,
  addUserMessage,
  addResponseMessage,
} from "react-chat-widget";
import { marked } from "marked";
import * as DOMPurify from "dompurify";
import $ from "jquery";

import "react-chat-widget/lib/styles.css";

/* OBSERVERS */
function adjustHeightWhenResize() {
  if ($(window).height() >= 850) {
    // 720px
    $(".rcw-conversation-container").height("620px");
    $(".rcw-messages-container").height("502px");
  } else {
    $(".rcw-conversation-container").height(`${$(window).height() - 210}px`); // 110px
    $(".rcw-messages-container").height(`${$(window).height() - 328}px`);
  }
}

/**
 * Watch for appearance of chat header and add listener to resize depending on window height.
 */
const rcwHeaderMutationObserver = new MutationObserver(function () {
  if (
    document.contains(
      document.querySelector(".rcw-conversation-container .rcw-header")
    )
  ) {
    adjustHeightWhenResize();
    window.addEventListener("resize", adjustHeightWhenResize, true);
    $(".rcw-disclaimer-info").parent()[0].style.height = "49px";
    rcwHeaderMutationObserver.disconnect();
  }
});

/**
 * Watch for when the footer intersects with the viewport, and move floating agent accordingly.
 */
const footerObserver = new IntersectionObserver(
  function (entries) {
    if (entries[0].isIntersecting) {
      $(".rcw-widget-container").css("position", "absolute");
    } else if (!entries[0].isIntersecting) {
      $(".rcw-widget-container").css("position", "fixed");
    }
  },
  { threshold: [0] }
);

/* HELPER FUNCTIONS */
function isMobile() {
  return (
    navigator.userAgent.match(/Android/i) ||
    navigator.userAgent.match(/webOS/i) ||
    navigator.userAgent.match(/iPhone/i) ||
    navigator.userAgent.match(/iPad/i) ||
    navigator.userAgent.match(/iPod/i) ||
    navigator.userAgent.match(/BlackBerry/i) ||
    navigator.userAgent.match(/Windows Phone/i)
  );
}

function removeLoadingMessage() {
  if (document.querySelector(".rcw-loading")) {
    document.querySelector(".rcw-loading").parentElement.remove();
  }
}

function giveNoAnswer(isAzureGcpRelated) {
  removeLoadingMessage();
  if (isAzureGcpRelated) {
    renderCustomComponent(GcpAzureRelatedResponse, {}, true);
  } else {
    renderCustomComponent(NoAnswerFound, {}, true);
  }
}

function receivedFeedbackHandler(thumbsUp, clicked) {
  if (clicked) {
    return;
  }
  addUserMessage(thumbsUp ? "üëç" : "üëé");
  // SEND INFO TO BACKEND @quinn.leng
  addResponseMessage(
    thumbsUp
      ? "Glad to hear that. What else can I help you with?"
      : "I'm sorry to hear that. What else can I help you with?"
  );
}

/* CUSTOM COMPONENTS */
function Disclaimer() {
  return (
    <div className="rcw-disclaimer-info">
      <svg
        width="1em"
        height="1em"
        viewBox="0 0 16 16"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        aria-hidden="true"
        focusable="false"
        className="rcw-info"
      >
        <path
          d="M7.25 11V7h1.5v4h-1.5zM8 4.5A.75.75 0 118 6a.75.75 0 010-1.5z"
          fill="currentColor"
        ></path>
        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M0 8a8 8 0 1116 0A8 8 0 010 8zm8-6.5a6.5 6.5 0 100 13 6.5 6.5 0 000-13z"
          fill="currentColor"
        ></path>
      </svg>
      <div>
        This trained assistant model may display inaccurate or offensive
        information that doesn‚Äôt represent Databricks views.
      </div>
    </div>
  );
}

function LoadingMessage() {
  return <div className="rcw-response rcw-message-text rcw-loading"></div>;
}

function Introduction() {
  return (
    <div className="rcw-response">
      <div className="rcw-message-text">
        Hi, I'm the Databricks AI assistant. You can ask me any questions you
        have about using Databricks. <br />
        <br /> Examples: <br /> 1. How do I load files into Databricks? <br />{" "}
        2. How do you deploy my model for serving? <br /> 3. How can I convert
        my files to parquet files?
      </div>
    </div>
  );
}

function NoAnswerFound() {
  return (
    <div className="rcw-response">
      <div className="rcw-message-text">
        Sorry, I can't find an answer to the question you asked. But you may be
        able to find the answer in{" "}
        <Link
          url="https://docs.databricks.com"
          name="Databricks Documentation"
        />{" "}
        or{" "}
        <Link
          url="https://kb.databricks.com"
          name="Databricks Knowledge Base"
        />
      </div>
    </div>
  );
}

function GcpAzureRelatedResponse() {
  return (
    <div className="rcw-response">
      <div className="rcw-message-text">
        As an AI assistant, I'm currently trained on documentation and knowledge
        base about Databricks on AWS. Please refer to the documentation for{" "}
        <Link
          url="https://learn.microsoft.com/en-us/azure/databricks"
          name="Databricks on Azure"
        />{" "}
        and{" "}
        <Link url="https://docs.gcp.databricks.com" name="Databricks on GCP" />{" "}
        in the meantime.
      </div>
    </div>
  );
}

function QuestionSeparator() {
  return <hr className="rcw-question-separator" />;
}

function Launcher(handleToggle) {
  const [showChat, setShowChat] = useState(false);

  const toggleChat = () => {
    setShowChat(!showChat);
    handleToggle();
    rcwHeaderMutationObserver.observe(document.querySelector("#rcw-root"), {
      subtree: true,
      childList: true,
    });
  };

  return (
    <button className="rcw-custom-launcher" onClick={toggleChat}>
      {showChat ? (
        <img
          className="rcw-close-launcher"
          src="https://agao00.github.io/static/media/chevron-down.041d46cd.svg"
          alt="close"
        />
      ) : (
        <>
          <img
            className="rcw-open-launcher"
            src="https://agao00.github.io/static/media/speech-bubble-plus.6df5d2c6.svg"
            alt="open"
          />
          <div className="rcw-tooltip">
            Ask a question to Databricks AI assistant
          </div>
        </>
      )}
    </button>
  );
}

function Link({ url, name }) {
  return (
    <a
      href={url}
      className="rcw-link"
      target="_blank"
      rel="noopener noreferrer"
    >
      {name ? name : url}
    </a>
  );
}

function ResponseWithLinks({ answer, sources }) {
  return (
    <div className="rcw-response">
      <div
        className="rcw-message-text"
        dangerouslySetInnerHTML={{
          __html: DOMPurify.sanitize(marked.parse(answer)),
        }}
      ></div>
      {sources.length > 0 ? (
        <div className="rcw-sources">
          <div className="rcw-sources-title">{`Source${
            sources.length === 1 ? "" : "s"
          }`}</div>
          {sources.map((src) => (
            <Link url={src}></Link>
          ))}
        </div>
      ) : null}
    </div>
  );
}

function Feedback() {
  const [clicked, setClicked] = useState(false);

  return (
    <>
      <div
        className="rcw-response rcw-feedback rcw-thumbs-up"
        onClick={() => {
          receivedFeedbackHandler(true, clicked);
          setClicked(true);
        }}
      >
        üëç
      </div>
      <div
        className="rcw-response rcw-feedback rcw-thumbs-down"
        onClick={() => {
          receivedFeedbackHandler(false, clicked);
          setClicked(true);
        }}
      >
        üëé
      </div>
    </>
  );
}

/* MAIN CLASS */
export class LLMAssistantWidget extends Component {
  history = [];

  componentDidMount() {
    renderCustomComponent(Disclaimer);
    renderCustomComponent(Introduction, {}, true);
    document.addEventListener("DOMContentLoaded", () => {
      // observer.observe(document.querySelector("footer#footer"));
    });
  }

  addQuestionToChatHistory(question) {
    this.history.push({
      question: {
        question,
        timestamp: new Date().getTime(),
      },
    });
  }

  addAnswerToChatHistory(answer) {
    let recordedAnswer;
    if (answer === "NO_ANSWER") {
      recordedAnswer =
        "Sorry, I can't find an answer to the question you asked. But you may be able to find the answer in Databricks Documentation or Databricks Knowledge Base";
    } else if (answer === "AZURE_GCP_ANSWER") {
      recordedAnswer =
        "As an AI assistant, I'm currently trained on documentation and knowledge base about Databricks on AWS. Please refer to the documentation for Databricks on Azure and Databricks on GCP in the meantime.";
    } else {
      recordedAnswer = answer;
    }
    this.history[this.history.length - 1].answer = {
      answer: recordedAnswer,
      timestamp: new Date().getTime(),
    };
  }

  getChatHistory() {
    this.history = this.history.slice(-4);
    return this.history.slice(0, -1);
  }

  async fetchAnswer(question, finallyFunc) {
    fetch("https://chatapi.databricks.com/doc_qa", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        question,
        history: this.getChatHistory(),
      }),
    })
      .then((response) => {
        response.text().then((text) => {
          const parsedText = JSON.parse(text);

          if (parsedText.related_to_azure_gcp) {
            giveNoAnswer(true);
            this.addAnswerToChatHistory("AZURE_GCP_RELATED");
          } else if (parsedText.has_answer) {
            removeLoadingMessage();
            this.addAnswerToChatHistory(parsedText.answer);
            renderCustomComponent(ResponseWithLinks, parsedText, true);
            // renderCustomComponent(Feedback, {}, true);
          } else {
            giveNoAnswer(false);
            this.addAnswerToChatHistory("NO_ANSWER");
          }
        });
      })
      .catch(() => {
        giveNoAnswer(false);
        this.addAnswerToChatHistory("NO_ANSWER");
      })
      .finally(() => {
        removeLoadingMessage();
        finallyFunc();
      });
  }

  handleNewUserMessage = async (newMessage) => {
    renderCustomComponent(LoadingMessage, {}, true);
    document.querySelector(".rcw-sender").style.visibility = "hidden";
    this.addQuestionToChatHistory(newMessage);
    await this.fetchAnswer(newMessage, () => {
      document.querySelector(".rcw-sender").style.display = "none";
      setQuickButtons([
        {
          label: "Ask another question",
          value: "",
        },
      ]);
    });
  };

  handleQuickButtonClicked = () => {
    setQuickButtons([]);
    document.querySelector(".rcw-sender").style.visibility = "visible";
    document.querySelector(".rcw-sender").style.display = "flex";
    renderCustomComponent(QuestionSeparator);
  };

  render() {
    return isMobile() ? null : (
      <div className="LLMAssistantWidget">
        <Widget
          handleNewUserMessage={this.handleNewUserMessage}
          handleQuickButtonClicked={this.handleQuickButtonClicked}
          title="Databricks AI assistant"
          subtitle=""
          profileAvatar="https://agao00.github.io/static/media/dblogo.56ac0149.jpg"
          titleAvatar="https://agao00.github.io/static/media/dblogo.56ac0149.jpg"
          launcher={(handleToggle) => Launcher(handleToggle)}
        />
      </div>
    );
  }
}
